import React from "react";
import ReactDom from "react-dom";
class AgeChecker extends React.Component {
  constructor() {
    super();
    this.state = {
      name: "",
      yearofbirth: null,
      age: null,
      text: "",
      msg1: ""
    };
    this.updatename = this.updatename.bind(this);
    this.updatedob = this.updatedob.bind(this);
    this.checkage = this.checkage.bind(this);
  }
  updatename(e) {
    this.setState({ name: e.target.value });
  }
  updatedob(e) {
    this.setState({ yearofbirth: e.target.value });
  }
  checkage() {
    let cy = new Date();
    let agc = cy.getFullYear() - this.state.yearofbirth;
    const el = " Your age is ";
    const ad = "You are adult";
    const adn = "You are not adult";

    this.setState({
      age: agc,
      text: el
    });
    agc < 18 ? this.setState({ msg1: adn }) : this.setState({ msg1: ad });
  }

  render() {
    return (
      <div className="App form-group">
        <div className=" container-fluid header w-50">
          <div className="row ">
            <div className="col-lg-12">
              <h1 className="text-center">Age Calculator</h1>
            </div>
          </div>
          <div className="row p-3">
            <div className="col-lg-12">
              <input
                type="text"
                className="form-control   mx-auto inp1"
                placeholder="Full Name"
                required
                onChange={this.updatename}
              />
            </div>
          </div>
          <div className="row p-3">
            <div className="col-lg-12">
              <input
                type="text"
                className="form-control  mx-auto inp2"
                placeholder="Year Of Birth"
                onChange={this.updatedob}
                required
              />
            </div>
          </div>
          <div className="row">
            <div className="col-lg-12">
              <h4 className="text-center">
                {" "}
                {this.state.name}
                {this.state.text}
                {this.state.age}
              </h4>
              <h6 className="text-center">{this.state.msg1}</h6>
            </div>
          </div>
          <div className="row ">
            <div className="col-lg-12">
              <button
                type="button"
                className="btn btn-warning w-50 btn-pos"
                onClick={this.checkage}
                disabled={this.state.yearofbirth < 1}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default AgeChecker;
